.table {
  margin: 0 auto 2rem;
  width: 500px;
}
.table-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.table-row--warehouse-header {
  width: 400px;
  font-weight: bold;
}

.other-table {
  margin-top: 2rem;
}

form {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: 0 auto;
}

form * {
  margin-bottom: 0.7rem;
}
